// wxapi.js
import wx from 'weixin-js-sdk'
import fetch from './fetch'
const wxApi = {
  /**
   * [isweixin ]
   */
  isweixin() {
    const ua = window.navigator.userAgent.toLowerCase()
    console.log(ua)
    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
      return true
    } else {
      this.$router.push({
        path: '/wxkj/isnotWechat'
      })
      return false
    }
  },
  /**
   * [wxRegister Api]
   * @param  {Function} callback [ready]
   */
  wxRegister(callback) {
    let data = {
      params: {
        reqUrl: window.location.href
      }
    }
console.log(data.params)
    fetch.post(false, '/wxpl/cfgInfo', data).then((res) => {
      wx.config({
        debug: false, // 
        appId: res.appId, // 
        timestamp: res.timestamp, // 
        nonceStr: res.noncestr, // 
        signature: res.signature, // 1
        jsApiList: [
          'onMenuShareAppMessage', // 
          'onMenuShareTimeline' // 
        ] // JSJS2
      })
    }).catch((error) => {
      console.log(error)
    })
    wx.ready((res) => {
      // ready
      if (callback) {
        callback()
      }
    })
  },
  /**
   * [ShareTimeline ]
   * @param {[type]} opstion []
   * @param {[type]} success []
   * @param {[type]} error   []
   */
  ShareTimeline(opstion) {
    wx.onMenuShareTimeline({
      title: opstion.title, // 
      link: opstion.link, // 
      imgUrl: opstion.imgUrl, // 
      success() {
        // 
        opstion.success()
      },
      cancel() {
        // 
        opstion.error()
      }
    })
  },
  /**
 * [isWxAuth ]
 */
 isWxAuth () {
    let localUid = localStorage.getItem('localUid')
    let localToken = localStorage.getItem('localToken')
    if (!localToken) {
     let token = this.$route.query.token
     let uid = this.$route.query.uid
     if (token) {
      localStorage.setItem('localToken', token)
      localStorage.setItem('localUid', uid)
     } else {
      // urlVue hash #
      var u = encodeURIComponent(window.location.href)
      window.location.href = hostName + '/wxpl/oauth?forwardUrl=' + u
     }
    }
   }
}
export default wxApi



