import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import home from '@/views/home/index'
import personal from '@/views/personal/index'
import Assemble from '@/views/Assemble/index'
import order from '@/views/order/index'
import confirmationOfOrder from '@/views/confirmationOfOrder/index'
import addressManagement from '@/views/addressManagement/index'
import is404 from '@/views/page/is404'
import notfound from '@/views/page/notfound'

Vue.use(Router)

export default new Router({
  routes: [
    {
      // 
      path: '/',
      name: 'Assemble',
      meta: {
        title: '', 
      },
      component: Assemble
    },
    {
      // 
      path: '/personal/:obj',
      name: 'personal',
      meta: {
        title: '', 
      },
      component: personal
    },
    {
      // 
      path: '/Assemble/:obj',
      name: 'Assemble',
      meta: {
        title: '', 
      },
      component: Assemble
    },
    {
      //
      path: '/order/:obj',
      name: 'order',
      meta: {
        title: '', 
      },
      component: order
    },
    {
      //
      path: '/confirmationOfOrder/:obj',
      name: 'confirmationOfOrder',
      meta: {
        title: '', 
      },
      component: confirmationOfOrder
    },
    {
      // 
      path: '/HelloWorld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      // 
      path: '/addressManagement',
      name: 'addressManagement',
      component: addressManagement
    },
    {
      path:'/404',
      component: is404
    },
    {
      path:'/notfound',
      component: notfound
    }
  ]
})



